import { Component, OnInit } from '@angular/core';
import {NgbModule,NgbCarouselConfig , NgbCarousel, NgbSlideEvent, NgbSlideEventSource } from '@ng-bootstrap/ng-bootstrap';
import { ContentIssueService } from './content-issue.service';
import { Router, RouterModule ,ActivatedRoute , Event,NavigationEnd,NavigationStart} from '@angular/router';

import { RodmanCoreService } from '../../rodman-core.service';
import * as configVar from '../../rodman-core.service';



@Component({
  selector: 'CoreLib-content-issue',
  templateUrl: './content-issue.component.html',
  styleUrls: ['./content-issue.component.css'],
  providers: [NgbModule,NgbCarouselConfig],
})
export class ContentIssueComponent implements OnInit {
  magazineId;
  slug;
  issueData:any=[];
  sliderData:any=[];
  loading=true;
  currentURL;
  lastSlug;
  name;
  nextIssue;
  page =0;
  ptintTittle =false;  
  routeChange:boolean=true;   
  getAllAd:any=[];
  viewAdRender:number;
  
  constructor(public RodmanCoreService:RodmanCoreService, public ActivatedRoute:ActivatedRoute, public Router:Router,public ContentIssueService:ContentIssueService) { 

    Router.events.subscribe(event => {
      if(event instanceof NavigationStart) {
          let data = event.url.split('/');
          this.lastSlug = data[data.length-1];
          this.routeChange = true;
      }
      if(this.routeChange){
        this.issueData=[];
        // this.sliderData =[];
        this.getIssue(this.lastSlug);
        this.routeChange = false;
      }
    });

  }

  ngOnInit() {
    this.ActivatedRoute.data.subscribe(data => {
      this.magazineId=data.magazineId;
    })    
    this.getIssue(this.slug);
    this.getads();
    this.name = configVar.magazineIds[this.magazineId].name; 
  }

  getIssue(slug){
      this.ContentIssueService.getIssue(this.magazineId,slug).subscribe((data)=>{
        let collectData = data['data'];
        // if(this.page==0)  {
          this.sliderData = data['issue'];
          // console.log(this.sliderData)
          this.page = 1;
        // }      

        this.nextIssue = data['nextIssue'];
        for(let data of collectData){
          this.issueData.push(data);
        }
        this.loading=false;
      });
  }

  getads(){
    this.RodmanCoreService.getAllAdst(this.magazineId).subscribe((data =>{
      this.getAllAd = data;
      this.viewAdRender = 1;
   }))
  }

  loadMoreData(slug){
    this.loading=true;
    // this.ptintTittle=true;
    this.getIssue(slug);
  }

  safeHtml(data){
    return data.replace(/[^a-zA-Z0-9 ]/g, "")
  }

  titlePrint(title){
    return (title == 1) ?  true  : false ;
  }


}
